.dropdown {
  position: relative;
  width: 34px;
  height: 24px;
}
.dropdown:hover {
  cursor: pointer;
  background-color: #eef2f7;
}
.dropdown__title {
  display: flex;
  justify-content: center;
  align-items: baseline;
  border-radius: 4px;
  width: 34px;
  height: 24px;
  font-size: 30px;
  letter-spacing: -1.5px;
  line-height: 6px;
  color: #42526d;
  outline: none;
}
.dropdown__title::-moz-focus-inner {
  border: 0;
}
.dropdown__title.--active,
.dropdown__title:hover:not(:disabled) {
  background-color: #42526d;
  color: #fff;
}
.dropdown__list {
  border: 1px solid #d8dee6;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 4px 4px 0 rgba(0,0,0,0.21);
  padding: 5px 0;
  position: absolute;
  margin-top: 4px;
  right: 0;
  z-index: 1;
  width: 164px;
}
body.--mobile .dropdown__list {
  box-shadow: none;
}
body.--mobile .dropdown__list {
  padding: 0;
}
.dropdown__list__actionWrapper {
  font-size: 13px;
  line-height: 1.15;
  display: block;
  padding: 9px;
  text-decoration: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 32px;
  padding-left: 11px;
  padding-right: 11px;
  color: #182b4c;
  display: flex;
  align-items: center;
}
body.--mobile .dropdown__list__actionWrapper {
  font-size: 14px;
}
.dropdown__list__actionWrapper:hover {
  background-color: #f2f5f9;
  cursor: pointer;
}
body.--mobile .dropdown__list__actionWrapper {
  min-height: 40px;
  padding-left: 12px;
  padding-right: 12px;
}
