




















































































































































































































































































































































































@import '../styles/variables/colors.styl'
@import "../styles/variables/dropdown.styl"
@import '../styles/mixins/text.styl'
@import '../styles/mixins/utils.styl'

$rowChangeHighlightColor = $globalColorYellowOrange
$cellVerticalPadding = 10px

// Как бы фоллбэк на всякий случай, если кастомное свойство `--scrollbar-width`
// какое-то совсем неправильное или не задано.
$scrollbarWidth__fallback = 15px


.baseTable-wrapper
    width 100%
    overflow-x auto
    overflow-y hidden

.baseTable
    min-width 100%
    height 100%
    // `inline-flex`, а не `flex`, потому что с `flex` ширина `.baseTable`
    // (которая должна быть неограничена по горизонтали и скроллится в своем родителе)
    // становится по ширине своего родителя, типа блок, а с `inline-flex` - по ширине
    // своего контента - набора колонок с определенными ширинами,
    // которые в итоге определяют ширину таблицы. Так же в БО.
    display inline-flex
    flex-direction column
    position relative

.baseTable__header
    flex 0 0 auto
    display flex
    // `baseTable__row` ожидается в контейнере с вертикальной выкладкой,
    // поэтому даже здесь указываем `column`
    flex-direction column

.baseTable__body
    flex 1 1 auto
    display flex
    flex-direction column
    // При большом количестве элементов скролл появляется здесь,
    // а заголовок остается на месте
    overflow-y scroll

.baseTable__row
    flex 0 0 auto
    display flex
    text-decoration none
    border-bottom 1px solid $globalColorMystic

    // В теле таблицы используется постоянный вертикальный скролл,
    // поэтому, чтобы колонки в заголовке имели такую же ширину,
    // как в теле, добавляем отступ, имитирующий ширину скролла
    &.--header
        border-right var(--scrollbar-width, $scrollbarWidth__fallback) solid transparent

    &:hover:not(.--header):not(.--checked)
         background-color $globalColorCatskillWhite

    // Классы модификаторы состояния
    &.--checked
        background $globalColorBlackSqueeze


.baseTable__cell
    commonTextStyles()
    position relative
    padding $cellVerticalPadding 24px $cellVerticalPadding 0
    vertical-align middle
    text-align left
    color $globalColorNileBlue
    overflow-x hidden
    white-space normal
    text-overflow ellipsis

    &.--header
        display flex
        align-items center
        white-space nowrap

        .baseTable__cell__headerText
            font-weight bold

    &.--sortable
        cursor pointer

    &.--actions
        // См. коммент для `.baseTable__cell__actions`
        width $dropdownTriggerWidth

        // В общем случае для ячеек ставится `overflow-x hidden`,
        // но в ячейке экшнов выпадает абсолютно позиционируемый дропдаун
        overflow visible

.baseTable__cell__sortingIndicator
    $sortingIndicatorWidth = 8px

    flex 0 0 auto
    width $sortingIndicatorWidth
    height ($sortingIndicatorWidth * 2) + 2px
    margin 0 7px
    display flex
    flex-direction column
    justify-content space-between

    &::before
    &::after
        content ''
        border ($sortingIndicatorWidth / 2) solid transparent

    &::before
        border-bottom-color $globalColorMischka
    &::after
        border-top-color $globalColorMischka

    &.--ascending::before
        border-bottom-color $globalColorNileBlue
    &.--descending::after
        border-top-color $globalColorNileBlue


.baseTable__cell__actions
    // Чтобы наличие элемента не влияло на высоту строки таблицы,
    // которая определяется по тексту (и прочему содержимому) + вертикальным паддингам,
    // извлекаем из потока и позиционируем абсолютно.
    // `!important` чтобы перекрыть `position relative`
    // у более тяжелого селектора самого компонента
    position absolute !important
    top 'calc(50% - %s)' % ($dropdownTriggerHeight / 2)


// Имитация чекбокса
.baseTable__checkbox
    width 14px
    height @width
    color #ffffff
    fill $globalColorShakespeare

    .baseTable__cell &
        margin-left 10px

