
















































@import "../styles/variables/buttons.styl"
@import "../styles/variables/colors.styl"
@import "../styles/mixins/utils.styl"
@import "../styles/mixins/text.styl"
@import "../styles/mixins/spinner.styl"

disabledStyles()
    color $globalColorLoblolly
    background-color $globalColorCatskillWhite
    cursor default


.button
    commonTextStyles()
    colorsStyles($globalColorCatskillWhite, $globalColorComet)
    display flex
    align-items center
    height $buttonHeight
    padding 0 12px
    border none
    border-radius 4px
    font-weight 500
    cursor pointer
    transition background-color .2s

    &:hover:not(:disabled),
    &:focus,
    &:active
        outline none
        background-color $globalColorMystic

    &:disabled
        disabledStyles()

    & .button__loadingIndicator
        margin-right 10px
        spinner(20px, 1px)

    &.--primary
        colorsStyles($globalColorCobalt, $globalColorWhite)

        &:hover:not(:disabled),
        &:focus,
        &:active
            outline none
            background-color $globalColorBlueRibbon

        &:disabled
            disabledStyles()


    &.--danger
        colorsStyles(#FF7272, $globalColorWhite)

        &:hover:not(:disabled),
        &:focus,
        &:active
            outline none
            background-color #ff4d4f

        &:disabled
            disabledStyles()

