































































































































































































































@import '../styles/variables/colors.styl'
@import '../styles/variables/dropdown.styl'
@import '../styles/variables/controlsInputs.styl'
@import '../styles/mixins/controlsInputs.styl'
@import '../styles/mixins/dropdown.styl'
@import '../styles/mixins/utils.styl'
@import '../styles/mixins/text.styl'


// Селектор `/deep/` добавлен элементу `multiselect-wrapper` и от него каскадом определены
// все стили, по следующей причине.
// Если для правила `.multiselect /deep/` есть 'дочернее' правило, например, `&.--hiddenList`,
// то результатом компиляции будет - `.multiselect[data-v-0306e1ae] .\--hiddenList`,
// то есть после `[data-v-0306e1ae]` добавится символ пробела, и селектор будет работать
// не как ожидается. Причин такого поведения я не знаю.
.multiselect-wrapper /deep/
    width 100%

    // Ресеты дефолтных стилей и применение миксинов
    .multiselect
        width 100%
        min-height $baseInputHeight
        // Чтобы потомки с `z-index` не налезали на внешние элементы с z-index.
        // (Например, `TestControls.vue`, 2 контрола подряд со значением -
        // икнока "стереть" второго накладывалась бы на выпадающий список первого)
        isolation isolate

        body.--mobile &
            min-height $baseMobileInputHeight


        // Блок с индикатором открытия
        .multiselect__select
            background none
            height $baseInputHeight
            width 28px
            min-height @height

            &::before
                listOpeningHalfMarker(9px, 14px, -45deg)

            &::after
                listOpeningHalfMarker(13px, 11px, 45deg)

            body.--mobile &
                height $baseMobileInputHeight

                &::before
                    listOpeningHalfMarker(16px, 14px, -45deg)

                &::after
                    listOpeningHalfMarker(20px, 11px, 45deg)


        // Инпут ввода
        .multiselect__input
            baseInputStyles()
            min-height 0
            height $tagHeight
            margin-bottom $tagInputVerticalOffset
            caret-color $globalColorNileBlue

            body.--mobile &
                height $mobileTagHeight
                margin-bottom $mobileTagInputVerticalOffset


        // Общие стили блока с плейсхолдером и блока куда выводится выбранное значение,
        // если выключен мультивыбор.
        .multiselect__single
        .multiselect__placeholder
            commonTextStyles()
            textOverflow()
            line-height $tagHeight
            margin 0
            padding 0 40px 0 0 // расстояние под абсолютно-позиционированные иконки справа
            background-color transparent

            body.--mobile &
                line-height $mobileTagHeight


        // Блок куда выводится выбранное значение, если выключен мультивыбор.
        .multiselect__single
            color $globalColorNileBlue


        // Блок с плейсхолдером
        .multiselect__placeholder
            color $globalColorLabel


        .multiselect__tags-wrap
            display flex
            flex-wrap wrap
            padding-right 50px


        .multiselect__tags
            baseInputTagsContainerStyles()


        .multiselect__tag
            baseInputTagsElementStyles()
            commonTextStyles()
            display flex
            align-items center
            padding 0 $tagIconContainerWidth 0 4px
            border-radius 2px
            background-color #8591A3
            color $globalColorWhite

            span
                textOverflow()


        .multiselect__tag-icon
            height 100%
            display flex
            justify-content center
            align-items center
            width $tagIconContainerWidth
            background none !important // для всех последующийх селекторов с высшей специфичностью

            // Иконка - `content: "\D7"`
            &::after
                font-size 18px
                line-height 1
                font-weight 300
                color $globalColorMischka

            &.--clearValueModel
                position absolute
                right 25px
                height $baseInputHeight
                // т.к. `multiselect__tags` и текущий накладываются друг на друга,
                // надо дать этому приоритет
                z-index 1

                &::after
                    color $globalColorNileBlue

                body.--mobile &
                    height $baseMobileInputHeight


            &:hover
                &::after
                    color $globalColorBittersweet


        // Индикатор загрузки
        .multiselect__spinner
            top 0
            right 21px
            height $baseInputHeight
            width @height
            background none

            body.--mobile &
                right 15px
                height $baseMobileInputHeight
                width @height


        // Стили выпадающего списка
        .multiselect__content-wrapper
            dropdownListContainer()
            margin $dropdownListVerticalOffset 0
            overflow-x hidden
            overflow-y auto

            body.--mobile &
                margin 0
                border 1px solid $globalColorGeyser


        .multiselect__content
            dropdownList()
            max-width 100%


        .multiselect__element

            // Селектор `& + &` использовать нельзя, из-за бага при компиляции -
            // В скомпилированном css, будет селектор `/deep/`, который не распознается браузером.
            // Поэтому используем селектор `.multiselect__element`.
            body.--mobile & + .multiselect__element
                border-top 1px solid $globalColorGeyser


        .multiselect__element[data-select]
            font-size 0
            background-color transparent


        .multiselect__option
            dropdownListItem()
            display flex
            align-items center
            padding-top 0
            padding-bottom 0

            // У блока может быть дочерний элемент, который был передан в слот.
            // В любом случае, он должен растягиваться на всю ширину.
            > *
                flex 1 1 auto


        .multiselect__option--highlight
            background-color $dropdownListItemHighlightBgColor


        .multiselect__option--selected
            background-color transparent
            color $globalColorLoblolly
            font-weight normal


        &.multiselect--disabled
            background none
            opacity unset

            // Окраска "иконки" раскрытия списка
            .multiselect__select
                &::before
                &::after
                    // Этот стиль должен перекрывать "мобильные стили"
                    background-color $globalColorLoblolly !important

            .multiselect__single
            .multiselect__placeholder
                baseInputDisabledStyles()


        &.multiselect--active
            // Отмена разворота "иконки" раскрытия списка при открытом списке
            .multiselect__select
                transform none

            .multiselect__tags
                baseInputContainerActiveStyles()


        &:hover:not(.multiselect--disabled):not(.multiselect--active)
            .multiselect__tags
                baseInputContainerHoverStyles()


        &.--hiddenList
            .multiselect__content-wrapper
                display none



    // В Safari на мобильных устройствах IOS,
    // для выбора элемента списка приходится 2 раза тапать на него.
    //
    // Эта проблема, ее причины и способы решения подробно описаны в
    // (https://css-tricks.com/annoying-mobile-double-tap-link-issue/)
    // C `vue-multiselect` есть эта проблема, она описана в
    // (https://github.com/shentao/vue-multiselect/issues/363)
    //
    // В 2 словах, причина проблемы в том, что у элемента с обработчиком тапа
    // есть дочерний или братский элемент, который в обычном состоянии скрыт,
    // но показывается при ховере. Код ниже скрывает, только на мобильных устройствах
    // скрывает подходящие под эти критерии псевдоэлементы
    body.--mobile &
        .multiselect__option--highlight:after
        .multiselect__option--selected:after
        .multiselect__option--selected.multiselect__option--highlight:after
            display none !important


// По причине коммента в начале раздела стилей выношу это объявление отдельно
.multiselect-wrapper.--invalid /deep/ .multiselect:not(.multiselect--disabled) .multiselect__tags
    baseInputContainerInvalidStyles()

