























































































@import "../styles/variables/colors.styl"
@import "../styles/variables/dropdown.styl"
@import '../styles/mixins/utils.styl'
@import "../styles/mixins/dropdown.styl"

.dropdown
    position relative
    width $dropdownTriggerWidth
    height $dropdownTriggerHeight

    &:hover
        cursor pointer
        background-color $globalColorCatskillWhiteLess


.dropdown__title
    display flex
    justify-content center
    align-items baseline
    border-radius 4px
    width $dropdownTriggerWidth
    height $dropdownTriggerHeight
    font-size 30px
    letter-spacing -1.5px
    line-height 6px
    color $globalColorFiord
    hideButtonOutline()

    &.--active,
    &:hover:not(:disabled)
        background-color $globalColorFiord
        color $globalColorWhite


.dropdown__list
    dropdownListContainer()
    dropdownList()
    position absolute
    margin-top $dropdownListVerticalOffset
    right 0
    z-index 1
    width 164px


.dropdown__list__actionWrapper
    dropdownListItem()
    display flex
    align-items center
