







































































@import '../styles/variables/colors.styl'
@import '../styles/variables/controlsInputs.styl'

.baseSwitch-w
    display inline-block
    position relative
    cursor pointer

    input
        display none

    &.--checked
        .baseSwitch
            background-color $globalColorCaribbeanGreen

        .baseSwitch__icon
            left $switchIconHorizontalOffset

        .baseSwitch__slider
            transform translateX($switchSliderTranslate)
            transition $baseInputTransitionDuration

    &.--unchecked
        .baseSwitch__icon
            right $switchIconHorizontalOffset

        .baseSwitch__slider
            transform translateX(0px)
            transition $baseInputTransitionDuration

    &.--disabled
        cursor default
        opacity 0.6


.baseSwitch
    width $baseSwitchWidth
    height $baseSwitchHeight
    background-color #D5D9DF
    border-radius 4px

.baseSwitch__icon
    position absolute
    top $switchIconVerticalOffset
    width $switchIconHeight
    height @width
    fill $globalColorWhite

.baseSwitch__slider
    position absolute
    width $switchSliderHeight
    height @width
    top $switchSliderVerticalOffset
    left $switchSliderHorizontalOffset
    background-color $globalColorWhite
    border-radius 2px
    transition $baseInputTransitionDuration
