.modal {
  padding: 24px 32px 16px;
}
.modal__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.modal__header .modal__header__title {
  color: #182b4c;
  font-size: 22px;
  font-weight: normal;
  line-height: 25px;
}
.modal__header .modal__header__icon {
  flex: 0 0 auto;
  width: 18px;
  height: 18px;
  margin-right: 12px;
  fill: #fda63b;
}
.modal__body {
  font-size: 13px;
  line-height: 1.15;
}
body.--mobile .modal__body {
  font-size: 14px;
}
.modal__footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  border-top: 1px solid #e5e9ef;
  padding-top: 16px;
}
.modal__footerButton:not(:first-child) {
  margin-left: 8px;
}
