












































































































































































@import '../styles/variables/colors.styl'

.basePagination
    display flex
    align-items center
    justify-items center

.basePagination__item
    color $globalColorCobalt

    &:not(:last-of-type)
        margin-right 16px

    &.--disabled
        pointer-events none
        .basePagination__itemLink
            color $globalColorLoblolly
            cursor default

    &.--active
        font-weight 700
        color $globalColorNileBlue

.basePagination__itemLink
    text-decoration none

    &:hover
        cursor pointer
        text-decoration underline

.basePagination__item
    &.--active
    &.--ellipsis
        .basePagination__itemLink:hover
            cursor default
            text-decoration none
