























































































@import '../styles/mixins/controlsInputs.styl'
@import '../styles/variables/controlsInputs.styl'

.baseInputPassword
    position relative
    width 100%

    // У инпута ввода пароля делаем паддинг справа, равный размеру кнопки "показать/скрыть пароль"
    .baseInputPassword__input
        padding-right $baseInputHeight

        body.--mobile &
            padding-right $baseMobileInputHeight


    &.--invalid:not(.--disabled)
        /deep/ .baseInput
            baseInputContainerInvalidStyles()


.baseInputPassword__changePasswordVisibilityBtn
    position absolute
    display flex
    align-items center
    top 0
    right 0
    height $baseInputHeight
    width @height
    border none
    background-color transparent
    cursor pointer

    &:disabled
        cursor auto

    body.--mobile &
        height $baseMobileInputHeight
        width @height


// Для выравнивания иконки
.baseInputPassword__icon
    margin-top 2px

