







































@import "../styles/variables/colors.styl"
@import '../styles/mixins/links.styl'

.baseInputFile
    &.--disabled
        pointer-events none
        .baseInputFile__inputTitle
            color $globalColorLoblolly
            cursor default

.baseInputFile__inputTitle
    commonLinksStyles()
    font-weight 500
    display inline-block
    padding 5px 0 0


.baseInputFile__inputHidden
    display none
