.baseSwitch-w {
  display: inline-block;
  position: relative;
  cursor: pointer;
}
.baseSwitch-w input {
  display: none;
}
.baseSwitch-w.--checked .baseSwitch {
  background-color: #00cb97;
}
.baseSwitch-w.--checked .baseSwitch__icon {
  left: 5px;
}
.baseSwitch-w.--checked .baseSwitch__slider {
  transform: translateX(16px);
  transition: 0.2s;
}
.baseSwitch-w.--unchecked .baseSwitch__icon {
  right: 5px;
}
.baseSwitch-w.--unchecked .baseSwitch__slider {
  transform: translateX(0px);
  transition: 0.2s;
}
.baseSwitch-w.--disabled {
  cursor: default;
  opacity: 0.6;
}
.baseSwitch {
  width: 40px;
  height: 24px;
  background-color: #d5d9df;
  border-radius: 4px;
}
.baseSwitch__icon {
  position: absolute;
  top: 7px;
