  height: 10px;
  fill: #fff;
}
.baseSwitch__slider {
  position: absolute;
  width: 16px;
  height: 16px;
  top: 4px;
  left: 4px;
  background-color: #fff;
  border-radius: 2px;
  transition: 0.2s;
}
