






































































































































@import "../styles/variables/colors.styl"
@import "../styles/mixins/text.styl"

.modal
    padding 24px 32px 16px

.modal__header
    display flex
    align-items center
    margin-bottom 20px

    .modal__header__title
        color $globalColorNileBlue
        font-size 22px
        font-weight normal
        line-height 25px

    .modal__header__icon
        flex 0 0 auto
        width 18px
        height @width
        margin-right 12px
        // todo вынести `fill` в класс-модификатор, который следует определять по `headerIconType`
        fill $globalColorYellowOrange

.modal__body
    commonTextStyles()

.modal__footer
    display flex
    justify-content flex-end
    margin-top 20px
    border-top 1px solid $globalColorMystic
    padding-top 16px

.modal__footerButton
    &:not(:first-child)
        margin-left 8px
