



















































































































































































































































































@import '../styles/mixins/controlsInputs.styl'
@import '../styles/variables/controlsInputs.styl'
@import '../styles/variables/colors.styl'

.baseDatePicker
    baseInputContainerStyles()
    width 100% !important // перекрытие библиотечного

    &:hover:not(.--disabled):not(.is-active)
        baseInputContainerHoverStyles()

    &.is-active:not(.--disabled)
        baseInputContainerActiveStyles()

    &.--disabled
        border-color $globalColorInput
        color $globalColorTextDisabled

        /deep/ input
        /deep/ .el-input__icon
            color $globalColorLoblolly

    & /deep/
        input
            baseInputStyles()

        .el-input__icon
            color $globalColorNileBlue
            line-height normal
            margin 0

        // В Safari на мобильных устройствах IOS,
        // для открытия панели пикера и выделения даты приходится
        // 2 раза тапать на элементы интерфейса.
        //
        // Эта проблема, ее причины и способы решения подробно описаны в
        // (https://css-tricks.com/annoying-mobile-double-tap-link-issue/)
        // В 2 словах, причина проблемы в том, что у элементов
        // (.el-input__suffix-inner .el-input__icon) и
        // (.el-input__suffix-inner .el-input__icon.el-icon-circle-close)
        // есть псевдоэлементы, которые в обычном состоянии скрыты,
        // но показываются при ховере. Код ниже скрывает, только на мобильных устройствах
        // скрывает подходящие под эти критерии псевдоэлементы
        body.--mobile & .el-input__suffix-inner .el-input__icon
            &::before
            &::after
                display none

            &.el-icon-circle-close
                width 25px
                height 100%
                background-image: url('../assets/fallback-close-icon.png')
                background-position center
                background-repeat no-repeat
                background-size 16px
                transition none

                &::before
                &::after
                    display none


        .el-range-separator
            padding 0
            line-height $baseInputHeight - ($baseInputBorder * 2)

            body.--mobile &
                line-height $baseMobileInputHeight - ($baseInputBorder * 2)


    // Дополнительные стили для контрола без выбора периода
    // В таких контролах уменьшение контейнера пикера не уменьшает инпут внутри,
    // а также иконка абсолютно позиционирована и перекрывает инпут
    &:not(.el-range-editor) /deep/
        input
            baseInputStyles()
            height 100%
            margin-left 27px


