

































































































































@import '../styles/variables/colors.styl'
@import '../styles/variables/controlsInputs.styl'
@import '../styles/mixins/controlsInputs.styl'
@import '../styles/mixins/utils.styl'

$buttonSize = 20px

.baseInputNumber
    baseInputContainerStyles()
    width 100%
    display flex
    align-items center

    &.--active
        baseInputContainerActiveStyles()

    &:hover:not(.--disabled):not(.--active)
        baseInputContainerHoverStyles()

    &.--invalid:not(.--disabled)
        baseInputContainerInvalidStyles()


.baseInputNumber__button
    svgContainer()
    hideButtonOutline()
    flex 0 0 $buttonSize
    height $buttonSize
    padding 0
    border 0
    border-radius 3px
    background-color $globalColorCloudBurst
    line-height 1
    font-size 20px
    color $globalColorWhite
    transition background-color $baseInputTransitionDuration

    // Так как инпут находится на первом месте в шаблоне, нам необходимо управлять порядком кнопок
    &.--decrement
        margin-right $baseInputPaddingHorizontal
        order 1

        body.--mobile &
            margin-right $baseMobileInputPaddingHorizontal

    &.--increment
        margin-left $baseInputPaddingHorizontal
        order 3

        body.--mobile &
            margin-left $baseMobileInputPaddingHorizontal

    &:hover:not(:disabled)
    &:focus
        background-color $globalColorCobalt
        cursor pointer

    svg
        fill $globalColorWhite

.baseInputNumber__input
    baseInputStyles()
    hideInputNumberSpinners()
    flex 1 1 auto
    align-self stretch
    order 2
    min-width 0

    &:disabled
        baseInputDisabledStyles()

