

















































@import '../styles/mixins/controlsInputs.styl'
@import '../styles/mixins/utils.styl'
@import '../styles/mixins/text.styl'

/* Дублирование класса для утяжеления специфичности и перекрытия стилей библиотеки */
.vue-input-tag-wrapper.vue-input-tag-wrapper
    baseInputTagsContainerStyles()
    // https://developer.mozilla.org/ru/docs/Web/CSS/-moz-appearance
    // Этот стиль используется в библиотеке `vue-input-tag` и перекрывает
    // наши собственные стили для инпута
    -webkit-appearance none

    &.vue-input-tag-wrapper--active
        baseInputContainerActiveStyles()

    &:hover:not(.--disabled):not(.vue-input-tag-wrapper--active)
        baseInputContainerHoverStyles()

    &.--invalid
        baseInputContainerInvalidStyles()

    /deep/ .new-tag
        baseInputStyles()
        // Хак, который делает так, что инпут занимает свободное пространство "строки".
        // Иначе, если больше половины "строки" занято тегами, он перенесется на следующую строку.
        // Как это работает я не понимаю.
        width 0
        margin-bottom $tagInputVerticalOffset

        body.--mobile &
            margin-bottom $mobileTagInputVerticalOffset


    /deep/ .input-tag
        baseInputTagsElementStyles()
        commonTextStyles()
        display flex
        align-items center
        padding 2px 4px
        colorsStyles($globalColorSlateGray, $globalColorWhite)
        border none
        border-radius 2px

        .remove
            display inline-block
            margin-top 2px
            margin-left 5px
            width 8px
            height @width
            background-image embedurl("../assets/icons/close-mischka.svg")

            &:hover
                background-image embedurl("../assets/icons/close-red.svg")

            // Необходимо для переопределения дефолтного стиля в используемой библиотеке
            &:before
                content ''
