<template>
    <table>
        <tbody>
            <tr class="colorPicker__row">
                <td
                    v-for="(color, index) in $props.palette"
                    :key="index"
                    :style="{ backgroundColor: color }"
                    :class="{ '--selected': color === $props.value }"
                    class="colorPicker__cell"
                    @click="selectColor(color)"
                >
                    <CheckIcon
                        v-if="color === $props.value"
                        class="colorPicker__selectionMarker"
                    />
                </td>
            </tr>
        </tbody>
    </table>
</template>

<script>
import CheckIcon from '../assets/icons/check.svg'


// eslint-disable-next-line max-len
const DEFAULT_PALETTE = ['#f85353', '#ff8135', '#ffad37', '#fdcb02', '#bdd743', '#85cf43', '#48c359', '#2cd2a1', '#30bec7', '#51a6e8', '#668af2', '#786bf4', '#b265fa', '#e868f4', '#afafaf', '#dddddd', '#e23232', '#d36524', '#de880d', '#ddb927', '#9db725', '#64af22', '#2e9c3d', '#0dac7d', '#1099a2', '#2383cd', '#4066d3', '#5143d9', '#862fd8', '#b52dc2', '#787878', '#4f4f4f']

export default {
    name: 'BaseColorPicker',

    components: {
        CheckIcon,
    },

    props: {
        // Выбранное пользователем значение цвета из палитры цветов
        value: {
            type: String,
            required: true,
        },

        // Переданная палитра цветов
        palette: {
            type: Array,
            required: false,
            default() {
                return DEFAULT_PALETTE
            },
        },
    },

    methods: {
        selectColor(color) {
            this.$emit('input', color)
        },
    },
}
</script>

<style scoped>
.colorPicker__row {
    display: flex;
    flex-wrap: wrap;
    width: 512px;
}
.colorPicker__cell {
    box-sizing: border-box;
    width: 32px;
    height: 32px;
    padding: 0;
}
.colorPicker__cell.--selected {
    border: 2px solid #4F4F4F;
}
.colorPicker__selectionMarker {
    margin: 30%;
    fill: #FFFFFF;
}
</style>
