









































































































































@import '../styles/variables/colors.styl'
@import '../styles/mixins/utils.styl'

.baseFormField
    display flex
    flex-direction column

    &:not(:first-child)
        margin-top 14px

.baseFormField__controlContainer
    display flex
    align-items center
    line-height 15px

    &.--none
        .baseFormField__label
            display none

    &.--top
        flex-direction column
        font-size 12px

        .baseFormFieldLabel__text
            color $globalColorSlateGray

    &.--side
        font-size 13px

        .baseFormFieldLabel__text
            color $globalColorCloudBurst

            &.--light
                color $globalColorSlateGray


.baseFormField__label
    display flex
    align-items center
    margin-right auto
    margin-bottom 4px
    padding-right 10px // для отступа от контрола


.baseFormField__control
    flex-shrink 0


.baseFormFieldLabel__tooltipIcon
    margin 0 auto 0 4px
    width 24px
    height @width


.baseFormField__message
    flex 1 0 auto
    margin-top 4px
    font-size 12px
    line-height 16px
    color $globalColorSlateGray

    &.--invalid
        color $globalColorBittersweet
